:root {
  --rain-bg-color: #2d3436;
  --rain-grad-first: #2d3436;
  --rain-grad-second: #ecf0f1;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100vh;
}

.rain-canvas {
  background-color: var(--rain-bg-color);
  background-image: linear-gradient(to bottom, var(--rain-grad-first), var(--rain-grad-second));
}

#root, #root > div {
  height: 100vh;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -999;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}